import React from "react"
import Layout from "../components/layout"

import { css } from "glamor"
import Link from "gatsby-link"

const authorsStyle = css({ display: `inline` })


export default ({ data }) => {
  const project = data.projectsJson

  const tmp = project.teaser ? (
    <div className="row">
        <div className="col-sm-12">
          <img className="img-responsive unpadded" src={__PATH_PREFIX__ + "projects/" + project.url + "/" + project.teaser}/>
        </div>
  </div>
  ) : (<div/>)

  
  var pdf_link = ''
  if (project.pdf) {
    pdf_link = project.pdf.startsWith('http') ? project.pdf : __PATH_PREFIX__ + "projects/" + project.url + "/" + project.pdf
  }

  return (
    <Layout>
    <div className="container">
      <div className="row col-sm-12">
      <h1>
        {project.name}
      </h1>
      </div>
      <div className="row col-sm-12">
        <p>
          {project.published}, {project.year}
        </p>
      </div>

      <div className="row col-sm-12">
      <ul className="authorlist">
      {project.authors.map(({ name }) =>
        <li className="list-inline-item authorlist">
            {name}{" "}
        </li>
      )}
      </ul>
      </div>
      {tmp}
      {project.teasercaption ?
        <p className="imagecaption">
      {project.teasercaption}
      </p> : <div/>}

      <div className="row col-sm-12">
      <h2>
        Abstract
      </h2>
      <p>
        {project.abstract}
      </p>
      </div>
      <div className="row col-sm-12">
      <h2>
        Data
      </h2>
      </div>
      <div className="row col-sm-12">
        <p>
      <a href={__PATH_PREFIX__ + "projects/" + project.url + "/citation.bib"}>Bibtex </a> 
      {project.pdf?<a href={pdf_link}>Paper </a>: false}
      {project.supplemental?<a href={project.supplemental}>Supplemental </a>: false}
      {project.video?<a href={project.video}>Video </a>: false}
      {project.slides?<a href={__PATH_PREFIX__ + "projects/" + project.url + "/" + project.slides}>Slides </a>: false}  
      </p> 
      </div>
    </div>
    </Layout>
  )
}

export const query = graphql`
  query ProjectQuery($slug: String!) {
    projectsJson(url: { eq: $slug } ) {
      name
      abstract
      authors {
        name
      }
      published
      year
      url
      supplemental
      video
      slides
      teaser
      teasercaption      
      pdf
    }
  }`